body {
  margin: 0;
  background: whitesmoke;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
	color: black;
	overflow-x: hidden;
}
.button-hover {
	background-color: #0a32b8;
}
.button-delete-hover {
	color: red;
	border-color: red;
}
.input {
	border-color: #07237e;
	border-radius: 5px;
	height: 1.6rem;
	outline: none;
}
.button {
	border: none;
	background-color: #07237e;
	color: whitesmoke;
	padding: 10px 20px;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	border-radius: 5px;
}
.button:hover {
	background-color: #0a32b8;
}
.col {
	position: fixed;
	top: 0;
	left: 0;
	width: 250px;
	height: 100vh;
	background-color: lightgray;
}

@media (max-width: 950px) {
	.col {
		display: none;
	}
	.post {
		margin: 0 !important;
	}
}
