.block {
  max-width: 700px;
  margin: 10px 0 0;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  grid-gap: 10px;
  gap: 10px;
}
.block p,
.block h2 {
  margin: 0;
  flex-shrink: 1;
}
.block__subtitle {
  font-size: x-large;
}
.block__button {
  border: none;
  background-color: #07237e;
