.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1;
  display: none;
  background-color: rgba(0, 0, 0, 0.5);
}
.show {
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-body {
  display: flex;
  flex-direction: column;
  grid-gap: 0;
  gap: 0;
  margin: 10px;
  align-items: center;
}
.modal-body__button-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  grid-gap: 0;
  gap: 0;
  justify-content: space-around;
}
.modal-body__button {
  border: none;
  background-color: #07237e;
  color: whitesmoke;
  padding: 10px 20px;
  width: 75px;
  border-radius: 5px;
  padding: 5px 10px;
}
.modal-body__button:hover {
  background-color: #0a32b8;
}
.modal-body__button--delete {
  border: none !important;
  background-color: #07237e !important;
  color: whitesmoke !important;
  padding: 10px 20px !important;
  width: 75px !important;
  border-radius: 5px !important;
  padding: 5px 10px !important;
  background-color: pink !important;
  color: darkred !important;
  border: 1px solid darkred !important;
}
