.App {
	color: black;
	overflow-x: hidden;
}
.button-hover {
	background-color: #0a32b8;
}
.button-delete-hover {
	color: red;
	border-color: red;
}
.input {
	border-color: #07237e;
	border-radius: 5px;
	height: 1.6rem;
	outline: none;
}
.button {
	border: none;
	background-color: #07237e;
	color: whitesmoke;
	padding: 10px 20px;
	width: fit-content;
	border-radius: 5px;
}
.button:hover {
	background-color: #0a32b8;
}
.post {
	margin-left: 250px;
	padding: 10px;
	padding-top: 0;
	display: flex;
	flex-direction: row;
	gap: 10px;
	flex-direction: row-reverse;
	flex-wrap: wrap;
	justify-content: flex-end;
}
.post article {
	max-width: 100%;
}
